import { Component } from '@angular/core';
import { IonicPage, NavParams, ViewController, NavController, LoadingController, Events, ToastController } from 'ionic-angular';
// import { PitaskinfoPage } from '../../pi/pitask/pitaskinfo/pitaskinfo';
// import { PiTasklocPage } from '../../../pages/pi/pitask/pi-taskloc/pi-taskloc';
@IonicPage()
@Component({
  selector: 'page-dialog-pitaskitems',
  templateUrl: 'dialog-pitaskitems.html',
})
export class DialogPitaskitemsPage {
  public title: string;
  public errorDescription: string;
  public isshow: boolean;
  public taskid: string;
  constructor(public navParams: NavParams,
    private view: ViewController,
    private navCtrl: NavController,
    public loadingCtrl:LoadingController,
    public event:Events,
    public toastCtrl:ToastController) {
    this.title = this.navParams.get('title');
    this.errorDescription = this.navParams.get('errorDescription');
    this.isshow = this.navParams.get('isshow');
    this.taskid = this.navParams.get('taskid');
  }

  ionViewDidLoad() {
    let contentloading=this.loadingCtrl.create({
      content:'请等待...',
    })
    contentloading.present();
    this.event.subscribe('commit',(item)=>{
      console.log(item);
      if(item=='ok'){
        contentloading.dismiss();
        this.toastCtrl.create({
          message:'保存成功',
          duration:2000,
        }).present();
      }else if(item=='no'){
        contentloading.dismiss();
        this.toastCtrl.create({
          message:'保存失败',
          duration:2000
        }).present();
      }
      this.event.unsubscribe('commit');
    })
    this.event.subscribe('navCtrl',(item)=>{
      this.navCtrl=item;
    })
  }
  //关闭modal
  closeModal() {
      this.view.dismiss();
  }
  //返回上一层
  backTo() {
    this.view.dismiss();
    this.navCtrl.pop();
    // this.navCtrl.push(PiTasklocPage, {
    //   'taskid': this.taskid,
    //   'locid':this.navParams.get('locid')
    // })
    // .then(
    //   ()=>{
    //     this.view.dismiss();
    //   }
    // );
    // this.navCtrl.popTo(this.navCtrl.getByIndex(this.navCtrl.length()-3));
  }
  nextPiasset() {

  }
}



